.todoLoadingList {
   margin: 0;
   padding: 0 0 56px 0;
   list-style: none;
}

.todoLoadingItem {
   background-color: #fafafa;
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 24px;
   max-width: 570px;
   min-width: 90%;
   height: 72px;
   border-radius: 2px;
   box-shadow: 0 5px 25px rgba(73, 76, 83, 0.15);
}


.pulsate {
   background: linear-gradient(-45deg, #e9e9e9, #F0F0F0, #e9e9e9, #F0F0F0);
   background-size: 400% 400%;
   -webkit-animation: Gradient 2.25s ease infinite;
   -moz-animation: Gradient 2.25s ease infinite;
   animation: Gradient 2.90s ease infinite;
}

@-webkit-keyframes Gradient {
   0% {
      background-position: 0% 50%
   }

   50% {
      background-position: 100% 50%
   }

   100% {
      background-position: 0% 50%
   }
}

@-moz-keyframes Gradient {
   0% {
      background-position: 0% 50%
   }

   50% {
      background-position: 100% 50%
   }

   100% {
      background-position: 0% 50%
   }
}

@keyframes Gradient {
   0% {
      background-position: 0% 50%
   }

   50% {
      background-position: 100% 50%
   }

   100% {
      background-position: 0% 50%
   }
}